"use client"

import { useState } from "react"
import Link from "next/link"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { ScrollArea } from "@/components/ui/scroll-area"

// Bengali alphabet array
const bengaliAlphabet = [
  'অ', 'আ', 'ই', 'ঈ', 'উ', 'ঊ', 'ঋ', 'এ', 'ঐ', 'ও', 'ঔ',
  'ক', 'খ', 'গ', 'ঘ', 'ঙ',
  'চ', 'ছ', 'জ', 'ঝ', 'ঞ',
  'ট', 'ঠ', 'ড', 'ঢ', 'ণ',
  'ত', 'থ', 'দ', 'ধ', 'ন',
  'প', 'ফ', 'ব', 'ভ', 'ম',
  'য', 'র', 'ল',
  'শ', 'ষ', 'স', 'হ',
  'ড়', 'ঢ়', 'য়'
]

// Mock patient data - in a real app, this would come from an API
const mockPatients = [
  {
    name: "অভিজিৎ রায়",
    diaryNumber: "F/101",
    phone: "1234567890",
  },
  {
    name: "করুণা সেন",
    diaryNumber: "CH123",
    phone: "9876543210",
  },
  {
    name: "খগেন দাস",
    diaryNumber: "F/102",
    phone: "5555555555",
  },
  // Add more mock patients as needed
]

export default function DictionaryView() {
  const [selectedLetter, setSelectedLetter] = useState<string | null>(null)
  const [searchTerm, setSearchTerm] = useState("")
  
  // Filter patients based on selected letter and search term
  const filteredPatients = mockPatients.filter(patient => {
    const matchesLetter = selectedLetter 
      ? patient.name.startsWith(selectedLetter)
      : true
    const matchesSearch = searchTerm
      ? patient.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        patient.diaryNumber.toLowerCase().includes(searchTerm.toLowerCase()) ||
        patient.phone.includes(searchTerm)
      : true
    return matchesLetter && matchesSearch
  })

  return (
    <div className="container mx-auto px-4 py-8">
      <Card className="p-6 bg-white">
        <h1 className="text-2xl font-bold text-gray-900 mb-6">
          Patient Dictionary
        </h1>

        {/* Search Bar */}
        <div className="mb-6">
          <Input
            type="text"
            placeholder="Search by name, diary number, or phone"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="max-w-xl"
          />
        </div>

        {/* Bengali Alphabet Navigation */}
        <ScrollArea className="w-full mb-6">
          <div className="flex flex-wrap gap-2 pb-4">
            <Button
              variant={selectedLetter === null ? "default" : "outline"}
              onClick={() => setSelectedLetter(null)}
              className="min-w-[40px]"
            >
              All
            </Button>
            {bengaliAlphabet.map((letter) => (
              <Button
                key={letter}
                variant={selectedLetter === letter ? "default" : "outline"}
                onClick={() => setSelectedLetter(letter)}
                className="min-w-[40px] font-bengali"
              >
                {letter}
              </Button>
            ))}
          </div>
        </ScrollArea>

        {/* Patient List */}
        <div className="space-y-4">
          {filteredPatients.length === 0 ? (
            <p className="text-center text-gray-500 py-4">
              No patients found
            </p>
          ) : (
            filteredPatients.map((patient) => (
              <Card
                key={patient.diaryNumber}
                className="p-4 hover:shadow-md transition-shadow"
              >
                <div className="flex items-center justify-between">
                  <div>
                    <h3 className="text-lg font-semibold font-bengali">
                      {patient.name}
                    </h3>
                    <div className="text-sm text-gray-500 space-x-4">
                      <span>{patient.diaryNumber}</span>
                      <span>{patient.phone}</span>
                    </div>
                  </div>
                  <div className="flex space-x-2">
                    <Link href={`/patient/${patient.diaryNumber}/visit-entry`}>
                      <Button variant="outline" size="sm">
                        Add Visit
                      </Button>
                    </Link>
                    <Link href={`/patient/${patient.diaryNumber}`}>
                      <Button variant="outline" size="sm">
                        View Record
                      </Button>
                    </Link>
                  </div>
                </div>
              </Card>
            ))
          )}
        </div>
      </Card>
    </div>
  )
}
